import React, { useState, useEffect, useCallback, useRef } from "react";

import "./HomePage.css";

import { Link } from "react-router-dom";

import Adsense from "../../components/Adsense/Adsense.js";
import PageFrame from "../../components/PageFrame/PageFrame.js";
import Content from "../../components/Contents/index.js";
import Label from "../../components/Labels/Label/Label.js";
import ImageLabel from "../../components/Labels/ImageLabel/ImageLabel.js";
import HashTagSet from "../../components/HashTags/HashTagSet/HashTagSet.js";
import Button from "../../components/Buttons/index.js";

import VersionData from "../../components/Versions/VersionData.js";
import getOperatingSystem from "../../utils/os.js";

import logoImage from '../../img/logo.png';


const HomePage = ({language}) => {
  const [showDownloadOptions, setShowDownloadOptions] = useState(false);
  const dropdownRef = useRef(null);

  useEffect(() => {
    const handleClick = (event) => {
      if (dropdownRef.current && !dropdownRef.current.contains(event.target)) {
        setShowDownloadOptions(false);
      }
    };

    document.addEventListener("mousedown", handleClick);

    return () => {
      document.removeEventListener("mousedown", handleClick);
    };
  }, []);

  const T = useCallback((en, ko) => {
    if (language === "en") {
      return en;
    }
    else {
      return ko;
    }
  }, [language]);

  const getDownloadUrl = useCallback((osType) => {
    const newUrl = new URL(window.location.href);
    newUrl.pathname = "/donate/";
    newUrl.searchParams.set("download", VersionData.latestVersion());
    newUrl.searchParams.set("type", osType.toLowerCase());
    return newUrl.pathname + newUrl.search;
  }, []);

  const getPath = useCallback((path) => {
    const newUrl = new URL(window.location.href);
    newUrl.pathname = path;
    newUrl.searchParams.delete("download");
    newUrl.searchParams.delete("type");
    return newUrl.pathname + newUrl.search;
  }, []);

  return (
    <div className="HomePage">
      <PageFrame>
        <Content.TitleBar>TwitchLink</Content.TitleBar>
        <Content.Area className="downloadLatestVersion" style={{padding: "30px"}}>
          <ImageLabel style={{padding: "15px", width: "200px", height: "200px"}} src={logoImage}/>
          <div className="infoArea" style={{marginLeft: "40px", width: "100%"}}>
            <div style={{display: "flex"}}>
              <Label style={{fontSize: "35pt"}}>TwitchLink</Label>
              <Label style={{marginTop: "30px", fontSize: "15pt", color: "gray"}}>{VersionData.latestVersion()}</Label>
            </div>
            <Label style={{fontSize: "15pt", color: "gray"}}>{T("Twitch Stream/Video/Clip Downloader", "트위치 생방송/비디오/클립 다운로더")}</Label>
            <Button.ButtonArea>
              <Button.LinkButton href="https://github.com/devhotteok/TwitchLink" target="_blank">{T("Source Code", "소스코드")}</Button.LinkButton>
              <div style={{position: "relative", display: "flex"}}>
                <div className="downloadButton">
                  <Link className="Button downloadPartButton" to={getDownloadUrl(getOperatingSystem())}>
                    <svg style={{
                      width: "30px",
                      height: "30px",
                      minWidth: "30px",
                      minHeight: "30px"
                    }} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                      <path d="M12 2.5C12.5523 2.5 13 2.94772 13 3.5V14.0858L16.2929 10.7929C16.6834 10.4024 17.3166 10.4024 17.7071 10.7929C18.0976 11.1834 18.0976 11.8166 17.7071 12.2071L12.7078 17.2064L12.7005 17.2136C12.5208 17.3901 12.2746 17.4992 12.003 17.5L12 17.5L11.997 17.5C11.8625 17.4996 11.7343 17.4727 11.6172 17.4241C11.502 17.3764 11.3938 17.3063 11.2995 17.2136L11.2922 17.2064L6.29289 12.2071C5.90237 11.8166 5.90237 11.1834 6.29289 10.7929C6.68342 10.4024 7.31658 10.4024 7.70711 10.7929L11 14.0858V3.5C11 2.94772 11.4477 2.5 12 2.5Z"/>
                      <path d="M3 20.5C2.44772 20.5 2 20.9477 2 21.5C2 22.0523 2.44772 22.5 3 22.5H21C21.5523 22.5 22 22.0523 22 21.5C22 20.9477 21.5523 20.5 21 20.5H3Z"/>
                    </svg>
                    {T(`Download for ${getOperatingSystem()}`, `${getOperatingSystem()}용 다운로드`)}
                  </Link>
                  <div className="Button downloadPartButton releasesPartButton" onClick={() => setShowDownloadOptions(true)}>
                    <svg style={{
                      width: "30px",
                      height: "30px",
                      minWidth: "30px",
                      minHeight: "30px"
                    }} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" stroke-width="1.5" fill="none" stroke="currentColor">
                      <path stroke-linecap="round" stroke-linejoin="round" d="M12 6.75a.75.75 0 1 1 0-1.5.75.75 0 0 1 0 1.5ZM12 12.75a.75.75 0 1 1 0-1.5.75.75 0 0 1 0 1.5ZM12 18.75a.75.75 0 1 1 0-1.5.75.75 0 0 1 0 1.5Z" />
                    </svg>
                  </div>
                </div>
                {
                  showDownloadOptions
                  ?
                  <div ref={dropdownRef} className="releasesDropdown">
                    <Link className="releasesDropdownOption" to={getDownloadUrl("Windows")}>
                      <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" fill="currentColor">
                        <path d="M31.87 30.58H244.7v212.81H31.87zM266.89 30.58H479.7v212.81H266.89zM31.87 265.61H244.7v212.8H31.87zM266.89 265.61H479.7v212.8H266.89z"/>
                      </svg>
                      {T("Download for Windows", "Windows용 다운로드")}
                    </Link>
                    <Link className="releasesDropdownOption" to={getDownloadUrl("macOS")}>
                      <svg style={{width: "32px", height: "32px"}} xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M12.152 6.896c-0.948 0-2.415-1.078-3.96-1.04-2.04 0.027-3.91 1.183-4.961 3.014-2.117 3.675-0.546 9.103 1.519 12.09 1.013 1.454 2.208 3.09 3.792 3.039 1.52-0.065 2.09-0.987 3.935-0.987 1.831 0 2.35 0.987 3.96 0.948 1.637-0.026 2.676-1.48 3.676-2.948 1.156-1.688 1.636-3.325 1.662-3.415-0.039-0.013-3.182-1.221-3.22-4.857-0.026-3.04 2.48-4.494 2.597-4.559-1.429-2.09-3.623-2.324-4.39-2.376-2-0.156-3.675 1.09-4.61 1.091zM15.53 3.83c0.843-1.012 1.4-2.427 1.245-3.83-1.207 0.052-2.662 0.805-3.532 1.818-0.78 0.896-1.454 2.338-1.273 3.714 1.338 0.104 2.715-0.688 3.559-1.701"></path>
                      </svg>
                      {T("Download for macOS", "macOS용 다운로드")}
                    </Link>
                    <Link className="releasesDropdownOption" to={getPath("/download/")}>
                      {T("View Other Versions", "다른 버전 더보기")}
                    </Link>
                  </div>
                  :
                  null
                }
              </div>
            </Button.ButtonArea>
          </div>
        </Content.Area>
        <Adsense
          name="TwitchLink_Home_1"
          style={{display: "block"}}
          dataAdSlot="7190249483"
          dataAdFormat="auto"
          dataFullWidthResponsive="true"
        />
        <Content.Area>
          <Label style={{margin: "0 0 30px 10px", fontSize: "20pt"}}>{T("Features", "특징")}</Label>
          <Content.Box>
            <Content.TextArea className="features">
              - {T("Stream Downloads", "생방송 다운로드")}
              <br/>
              - {T("Video Downloads", "비디오 다운로드")}
              <br/>
              - {T("Clip Downloads", "클립 다운로드")}
              <br/>
              - {T("Subscriber-Only Video Downloads", "구독자 전용 비디오 다운로드")}
              <br/>
              - {T("Scheduled Downloads", "예약 다운로드")}
              <br/>
              - {T("Thumbnail Downloads", "썸네일 다운로드")}
              <br/>
              - {T("Audio-Only Downloads", "오디오 모드 다운로드")}
              <br/>
              - {T("Video Unmuting", "비디오 음소거 해제")}
              <br/>
              - {T("Video Cropping", "비디오 자르기")}
              <br/>
              - {T("Channel Bookmarks", "채널 북마크")}
              <br/>
              - {T("Customizable Filename Templates", "사용자 지정 파일명 템플릿")}
              <br/>
              - {T("User-Friendly UI", "사용자 친화적 UI")}
              <br/>
              - {T("Language and Time Zone Settings", "언어 및 시간 설정")}
              <br/>
              - {T("Free and Open Source", "무료 및 오픈소스")}
            </Content.TextArea>
          </Content.Box>
        </Content.Area>
        <Adsense
          name="TwitchLink_Home_2"
          style={{display: "block"}}
          dataAdSlot="3251004473"
          dataAdFormat="auto"
          dataFullWidthResponsive="true"
        />
        <Content.Area className="faq">
          <Label style={{margin: "0 0 30px 10px", fontSize: "20pt"}}>{T("FAQ", "자주 묻는 질문")}</Label>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "Can TwitchLink download live broadcasts?",
                "TwitchLink는 생방송을 다운로드할 수 있나요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "Try TwitchLink's live download feature! Live broadcasts without replays can be downloaded in real time!",
                "TwitchLink의 실시간 다운로드 기능을 사용해보세요! 다시보기가 남지 않는 생방송도 실시간으로 다운로드할 수 있습니다!"
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "Can TwitchLink download subscribers-only videos?",
                "TwitchLink는 구독자 전용 영상을 다운로드할 수 있나요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "TwitchLink can download subscriber only videos.",
                "TwitchLink는 구독자 전용 영상도 다운로드할 수 있습니다."
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "My video has been muted! Is there any way to recover?",
                "동영상이 음소거되었습니다. 복구할 방법이 있을까요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "Try TwitchLink's unmute feature! It recovers the original file as much as possible!",
                "TwitchLink의 음소거 해제 기능을 사용해보세요! 가능한 한 원본 파일을 찾아 복구합니다!"
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "I'm an editor. I'm recording live broadcasts every time to get a video, but it's quite annoying. Can TwitchLink solve this?",
                "저는 편집자입니다. 편집 영상을 얻기 위해 매번 생방송을 녹화하고 있는데 상당히 귀찮습니다. TwitchLink가 이걸 해결해 줄 수 있나요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "You can schedule the download to start automatically when the live broadcast begins.",
                "생방송이 시작되면 자동으로 다운로드하도록 설정할 수 있습니다."
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "Some sounds are missing in replays due to soundtrack settings. What should I do?",
                "트랙 분리로 인해 다시보기에서 일부 소리가 나지 않습니다. 이런 경우는 어떻게 하나요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "Try TwitchLink's live download feature! You can save all sounds regardless of the soundtrack settings.",
                "TwitchLink의 실시간 다운로드 기능을 사용해보세요! 트랙 분리가 되는 생방송의 소리도 모두 다운로드할 수 있습니다!"
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "It's too slow to download large videos. Is there a solution?",
                "용량이 큰 동영상을 다운로드하는 것이 너무 느립니다. 해결책이 있나요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "You can set the download speed in settings.",
                "설정에서 다운로드 속도를 적절하게 설정할 수 있습니다."
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.Subtitle>
              {T(
                "It's annoying to search for channels every time. Is there any other good way?",
                "매번 채널을 검색하기 귀찮습니다. 다른 좋은 방법이 있을까요?"
              )}
            </Content.Subtitle>
            <Content.TextArea>
              {T(
                "Try TwitchLink's bookmarks feature! You can register your favorite channels and search easily!",
                "TwitchLink는 채널 북마크 기능을 제공합니다. 자주 찾는 채널을 등록하고 편하게 검색하세요!"
              )}
            </Content.TextArea>
          </Content.Box>
          <Content.Box>
            <Content.TextArea>
              <HashTagSet>
                {T(
                  "#TwitchLink #Twitch Downloader #Stream Download #Stream Recording #Scheduled Download #Automatic Recording #Video Download #Clip Download #Download Subscribers-Only Video #Twitch Video #Download Part Of Video #Twitch Video Download #Twitch Vod Download #Unmute Video",
                  "#트위치링크 #트위치 다운로더 #생방송 실시간 다운로드 #생방송 녹화 #예약 다운로드 #자동 녹화 #다시보기 다운로드 #클립 다운로드 #구독자 전용 다시보기 다운로드 #트위치 비디오 #비디오 부분 다운로드 #트위치 영상 다운로드 #트위치 다시보기 다운로드 #음소거 해제"
                )}
              </HashTagSet>
            </Content.TextArea>
          </Content.Box>
        </Content.Area>
      </PageFrame>
    </div>
  );
};

export default HomePage;